/* QRCodeGenerator.css */

.di1 {
  display: flex;
  flex-direction: column; /* Align elements in a column */
  align-items: center;
  justify-content: center;
  /* padding: 20px; */
  position: absolute;
  top: 35%;
  left: 27%;
  width: 50%;
  height: auto;
  }
  
  .inp {
    width: 45%;
    padding: 10px;
    margin-bottom: 10px;
    font-size: 16px;
    border: solid rgb(18, 13, 13) 2px;
    border-radius: 12px;
    background-color: rgb(241, 251, 250);
  }
  
  .butt {
    background: rgb(27, 193, 199);
    /* background: rgb(17, 140, 216); */

    color: white;
    border: solid rgb(25, 1, 1) 2px;
    border-radius: 12px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    width: 45%;
  }
  
 .show
 {
    margin-top: 6%;
    
 }


  